import {connect} from "react-redux";
import store from "../../store";
import EditGoalRow from "./EditGoalRow";
import {saveChecklistTarget} from "../../containers/cbChecklist/checkListMethods";

const Goals = () => {
    let allGoals = store.getState().siteModelChecklist.currentChecklistGoals ? store.getState().siteModelChecklist.currentChecklistGoals : [];
    let currentChecklist = store.getState().siteModelChecklist?.currentChecklist;
    allGoals.push({checklistuuid:currentChecklist.id,active:1});



    return <div>
        <h4>Goals</h4>
        <table className={"table table-bordered"}>
            <thead>
                <tr>
                    <th></th>
                    <th>Goal</th>
                    <th>Active</th>
                    <th></th>
                </tr>
            </thead>
            <tbody>
            {allGoals.map((item,idx)=>{
                return <EditGoalRow item={item} idx={idx} />
            })}
            </tbody>
        </table>

    </div>
}

const mapStateToProps = (state) =>{
    return {
        siteModelChecklist:state.siteModelChecklist,
        dataStore:state.dataStore,
        tabStore:state.tabStore
    };
};

export default connect(mapStateToProps,{})(Goals);